<template>
  <div class="d-flex gap-10">
    <div v-show="isLocal" class="connect-type" @click="toggle()">Localhost</div>
    <div v-show="!isLocal" class="connect-type" @click="toggle()">Ngrok</div>

    <div v-if="isBigImg" class="img-size" @click="toggleSize()">Big image</div>
    <div v-else class="img-size" @click="toggleSize()">Small image</div>

    <div v-if="isPost" class="img-size" @click="toggleMethod()">Post</div>
    <div v-else class="img-size" @click="toggleMethod()">Get</div>
  </div>

  <div class="row">
    <div class="col" v-on:click="onRestore">Restore</div>
    <div class="col" v-on:click="reset">Refresh</div>
  </div>
  <div class="hello">
    <img
      :key="i"
      v-for="(i, index) in imageDatas"
      @click="addReject(index)"
      :src="
        '[object Promise]' != imageDatas[index]
          ? imageDatas[index]
          : '/image-acceptor/favicon.ico'
      "
      :class="{
        reject:
          fileNameRejects.filter((e) => e == fileNameAccepts[index]).length ==
          1,
      }"
      :alt="
        '[object Promise]' != imageDatas[index]
          ? imageDatas[index]
          : '/image-acceptor/favicon.ico'
      "
    />
  </div>
  <div class="row">
    <div class="col" v-on:mousedown="onAccept" @mouseup="stopRun">Accept</div>
    <div class="col" v-on:click="onReject">Reject</div>
  </div>
  <div class="base" style="user-select: text">
    baseUrl: {{ baseUrl }} <br />
  </div>
  <div class="error">
    {{ err }}
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      fileNameAccepts: [],
      fileNameRejects: [],
      imageDatas: [],
      isLocal: false,
      isBigImg: true,
      isPost: true,
      isRun: false,
      err: "",
    };
  },
  computed: {
    baseUrl() {
      return this.isLocal ? "http://localhost:5000" : window.__baseUrl;
    },
  },
  created() {
    this.getNotAcceptedImage();
  },

  methods: {
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    toggleSize() {
      this.isBigImg = !this.isBigImg;
    },
    toggleMethod() {
      this.isPost = !this.isPost;
    },
    toggle() {
      this.isLocal = !this.isLocal;
    },
    async onRestore() {
      try {
        await axios.post(this.baseUrl + "/unverify-last-img");
        this.reset();
      } catch (e) {
        this.err = JSON.stringify(e);
      }
    },
    reset() {
      this.getNotAcceptedImage();
    },
    async onAccept() {
      try {
        this.isRun = true;
        while (this.isRun) {
          let tasks = [];

          for (let i = 0; i < this.fileNameAccepts.length; i++) {
            tasks.push(
              axios.post(
                this.baseUrl + "/accept-img/" + this.fileNameAccepts[i]
              )
            );
          }

          await Promise.all(tasks);

          await this.sleep(200);
        }
      } catch (e) {
        this.err = JSON.stringify(e);
        return;
      } finally {
        this.reset();
      }
    },
    stopRun() {
      this.isRun = false;
    },
    addReject(index) {
      var temp = this.fileNameAccepts[index];

      if (this.fileNameRejects.filter((e) => e == temp).length > 0) {
        this.fileNameRejects = this.fileNameRejects.filter((e) => e != temp);
      } else {
        this.fileNameRejects.push(temp);
      }
      console.log(this.fileNameRejects);
    },
    async onReject() {
      try {
        for (let i = 0; i < this.fileNameRejects.length; i++) {
          await axios.post(
            this.baseUrl + "/reject-img/" + this.fileNameRejects[i]
          );
        }
        this.reset();
      } catch (e) {
        this.err = JSON.stringify(e);
      }
    },
    async getNotAcceptedImage() {
      try {
        this.fileNameAccepts = [];
        this.fileNameRejects = [];
        this.imageDatas = [];

        const res = await axios.post(this.baseUrl + "/get-image-to-verify/10");
        if (res.status == 200 && res.data.Data.length) {
          for (let i = 0; i < res.data.Data.length; i++) {
            const imageUrl =
              this.baseUrl +
              (this.isBigImg ? "/get-image/" : "/get-sml-image/") +
              res.data.Data[i].file_name;

            if (this.isPost) {
              const tempRes = axios.post(imageUrl);

              this.imageDatas.push(tempRes);
            } else {
              this.imageDatas.push(imageUrl);
            }

            this.fileNameAccepts.push(res.data.Data[i].file_name);
          }
          if (this.isPost) {
            for (let j = 0; j < this.imageDatas.length; j++) {
              this.imageDatas[j] = (await this.imageDatas[j]).data.base64;
            }
          }
        }
      } catch (e) {
        this.err = JSON.stringify(e);
      }
    },
  },
};
</script>

